/**
 * Hide element while making it readable for screen readers
 * Shamelessly borrowed from HTML5Boilerplate:
 * https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css#L119-L133
 */
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: var(--c-primary) !important;
}

.text-bold {
  font-weight: bold;
}

.p--accent {
  color: var(--c-primary);
}

.p--bold {
  font-weight: bold;
}

.nowrap {
  white-space: nowrap;
}