/* Панель выбора темы */
.theme_panel {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  padding: 4px 0;
  box-shadow: 0 2px 2px -2px rgba(0, 0, 0, 0.52);
  text-align: center;
  line-height: 0;
}

.theme_color {
  padding: 0.8rem;
  border-radius: 4px;
  margin: 0 2px;
  cursor: pointer;
  display: inline-block;
  transition-duration: 0.2s;
  vertical-align: top;
}

.theme_color:hover,
.theme_color.active {
  padding: 1rem;
}

/**
* 5. Components
 */
/*Buttons reset*/
.btn {
  display: inline-block;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background: transparent;
  cursor: pointer;
  transition: all 0.2s;
  text-decoration: none;
  /* Theme styles*/
}
.btn:hover, .btn:focus {
  outline: none;
}

.btn--primary {
  background: var(--c-primary);
  color: #fff;
  padding: 15px 24px;
  border-radius: 40px;
  font-size: 12px;
  line-height: 16px;
  font-weight: bold;
}
@media (min-width: 1024px) {
  .btn--primary {
    font-size: 14px;
    line-height: 17px;
    padding: 15px 55px;
  }
}
.btn--primary:active {
  box-shadow: inset 0px -4px 0px var(--c-primary-dark);
}

.btn--light {
  border: 2px solid var(--c-primary-light);
  border-radius: 8px;
  color: var(--c-primary-dark);
  font-size: 14px;
  line-height: 17px;
  font-weight: bold;
  padding: 10px 18px;
}
.btn--light:hover {
  background: var(--c-primary);
  border-color: var(--c-primary);
  color: #fff;
}

.close {
  float: right;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: 0.5;
}
.close:hover {
  color: #000;
  text-decoration: none;
}
.close:not(:disabled):not(.disabled):hover, .close:not(:disabled):not(.disabled):focus {
  opacity: 0.75;
}

button.close {
  padding: 0;
  background: transparent;
  border: 0;
  appearance: none;
}

a.close.disabled {
  pointer-events: none;
}

