.nav__link:after {
  position: absolute;
  content: "";
  display: block;
  bottom: 0;
  left: 50%;
  right: 50%;
  height: 1px;
  background: var(--c-primary);
  opacity: 0;
  transition: all 0.2s;
}
.nav__link:hover::after, .nav__link:focus::after {
  left: 0;
  right: 0;
  opacity: 1;
}
