.nav-toggler {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  padding: 10px;
  border: 0;
  background-color: transparent;
}
@media (min-width: 1200px) {
  .nav-toggler {
    display: none;
  }
}

.nav-toggler span {
  position: relative;
  display: inline-block;
  width: 25px;
  vertical-align: bottom;
}

.nav-toggler span,
.nav-toggler span:before,
.nav-toggler span:after {
  background: var(--c-primary-dark);
  transition: background 0.3s 0.5s;
  height: 2px;
}

.nav-toggler span:before,
.nav-toggler span:after {
  position: absolute;
  content: "";
  left: 0;
  width: 100%;
  transform-origin: 50% 50%;
}

.nav-toggler span:before {
  top: 8px;
}

.nav-toggler span:after {
  top: -8px;
}

body {
  position: relative;
}

body:after {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  content: "";
  background-color: rgba(0, 0, 0, 0.75);
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s ease-in-out;
}

.mobile-menu {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  max-width: 320px;
  z-index: 10;
  visibility: hidden;
  transform: translateX(100%);
  transition: all 0.2s ease-in-out;
  background-color: #fff;
}

.mobile-menu__inner {
  background-color: #fff;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  padding: 100px 40px 30px;
}

body.is-menu-opened {
  overflow: hidden;
}

body.is-menu-opened:after {
  opacity: 1;
  z-index: 5;
  visibility: visible;
}

body.is-menu-opened .mobile-menu {
  transform: translateX(0);
  visibility: visible;
}

